@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

/* ------ Add your CSS below this line (or @import your other CSS files). ------- */

body{
	font-family: "Inter", sans-serif;
}

.tophat{
	background-image: url(../images/tophat-blob.svg);
	background-repeat: no-repeat;
	background-position: bottom center;
	background-size: 100% auto;
	color: #5D0074;
}

.deck{
	background-image: url(../images/deck-blob.svg);
	background-repeat: no-repeat;
	background-size: 100% auto;
	background-position: center 50%;
}

.deck img.back{
	max-width: 280px;
}

.cta{
	background-image: url(../images/bg-buy.svg);
	background-repeat: no-repeat;
	background-size: auto 100%;
	background-position: top center;
}

footer{
	background-image: url(../images/art-zig-pattern.svg);
	background-size: 35% auto;
	background-position: center center;
}

.check{
	position: relative;
}
.check:before{
	background-image: url(../images/icon-check.svg);
	background-size: 100% 100%;
	display: inline-block;
	content: " ";
	height: 3rem;
	width: 3rem; 
	margin-right: 1rem;

}

.play{
	
}

.rough-annotation{
	z-index: -1;
}
.rough-annotation path{
	transform: translateY(-15px);
}

.feature{

}

.feature img.cards{
	position: relative;
	z-index: 10;
}

.feature.one img.absolute{
	top: 0;
	transform: translate(-20%, -8%) rotate(-20deg);
	z-index: -1;
	max-width: 135%;
	width: 135%;
	pointer-events: none;
}

.feature.two img.absolute{
		top: 0;
		transform: translate(-10%, -10%) rotate(30deg);
		z-index: -1;
		width: 90%;
	}

.feature.three img.absolute{
	top: 0;
	transform: translate(-5%, 0);
	z-index: -1;
	width: 120%;
	max-width: 120%;
}

@media (min-width:767px){
	.feature.one img.absolute{
		transform: translate(-10%, -35%) rotate(0deg);
		max-width: 100%;
		width: 100%;
	}

	.feature.two img.absolute{
		top: 0;
		transform: translate(-10%, -30%) rotate(0deg);
		z-index: -1;
		width: 75%;
	}

	.feature.three img.absolute{
		top: 0;
		transform: translateY(-16.25%);
		z-index: -1;
		width: 100%;
		max-width: 100%;
	}
}

.dial{
	height: 24rem;
	overflow: hidden;
}
.dial img{
	width: 600%;
	max-width: 600%;
	animation: rotating 140s linear infinite;
	filter: drop-shadow(0px 5px 15px rgba(0,0,0,0.1));
}

@keyframes rotating {
  from {
    transform: translateX(-42%) rotate(0deg);
  }
  to {
    transform: translateX(-42%) rotate(-360deg);
  }
}

@media (min-width:767px){
	.tophat{
		background-image: url(../images/tophat-blob-lg.svg);
		background-position: top right;
		background-size: contain;
	}
	.deck{
		background-size: 100% auto;
		background-position: top center;
	}
	marquee img{
		width: 280px;
	}
}

.content p{
	margin-bottom: 2rem;
}

/** Deck spinner **/

.scene{
	height: 360px;
	position: relative;
}

.scene:after{
		content: " ";
		display: block;
		background: #000;
		left: 20%;
		right: 20%;
		top: 0;
		bottom: 0;
		position: absolute;
		filter: blur(30px);
		z-index: 9;
		opacity: 0.7;
	}

.box {
  width: 220px;
  height: 300px;
  position: relative;
  transform-style: preserve-3d;
  transform: translateZ(-50px);
  transition: transform 1s;
  animation: rotate 8s infinite; 
  margin: 0 auto;
}

.box.show-front  { transform: translateZ( -50px) rotateY(   0deg); }
.box.show-back   { transform: translateZ( -50px) rotateY(-180deg); }
.box.show-right  { transform: translateZ(-150px) rotateY( -90deg); }
.box.show-left   { transform: translateZ(-150px) rotateY(  90deg); }


.box__face {
  position: absolute;
  font-size: 40px;
  font-weight: bold;
  color: white;
  text-align: center;
}

.box__face--front,
.box__face--back {
  width: 220px;
  height: 300px;
  line-height: 300px;
}

.box__face--right,
.box__face--left {
  width: 140px;
  height: 300px;
  left: 80px;
  line-height: 300px;
}

.box__face--front  { background: #fff url(../images/deck/hi-rez/box-front-shade.png) no-repeat; background-size:cover; }
.box__face--right  { background: #fff url(../images/deck/hi-rez/box-right-shade.png) no-repeat; background-size:cover; }
.box__face--back   { background: #fff url(../images/deck/hi-rez/box-back-shade.png) no-repeat; background-size:cover; }
.box__face--left   { background: #fff url(../images/deck/hi-rez/box-left-shade.png) no-repeat; background-size:cover; }

.box__face--front  { transform: rotateY(  0deg) translateZ( 70px); }
.box__face--back   { transform: rotateY(180deg) translateZ( 70px); }

.box__face--right  { transform: rotateY( 90deg) translateZ(70px); }
.box__face--left   { transform: rotateY(-90deg) translateZ(150px); }



@keyframes rotate {
  0% {
    transform: translateZ( -50px) rotateY(0deg) scale(1.4);
  }
  
  70% {
    transform: translateZ( -50px) rotateY(360deg) scale(1.4);
  }
  
  100% {
    transform: translateZ( -50px) rotateY(360deg) scale(1.4);
  }
}

